const TITLE = 'Policy Priority Inference';

const DESCRIPTION = ['Welcome to the Policy Priority Inference (PPI) web app. PPI is an artificial intelligence framework that models the relationship between government spending and development outcomes in a multidimensional and highly interconnected environment. This app contains a simplified version of the PPI model where you can calibrate the associated parameters and run a set of policy experiments using real-world data in your web browser. All the data that you upload to the app will remain in your computer.'
,'To use the app, you need to go through the following five steps: (1) uploading data on development indicators; (2) uploading data on government expenditure; (3) uploading a network of interdependencies between the indicators; (4) calibrating the model parameters; and (5) running prospective simulations.'
, 'Each step provides instructions and a spreadsheet template that you should use to prepare your data. At the end of the workflow, you will be able to visualise your simulation outputs and download the relevant data. Should you require to perform more nuanced analysis, you can use the Python source code of PPI.'];

export const texts_about_info = {
  TITLE,
  DESCRIPTION
}
